import React from 'react';
import EducationCard from '../components/EducationCard';

const SkillsAndEducationScreen = () => {
    const modules = [
        "Learn Git Course",
        "Learn the Command Line Course",
        "Build a Website with HTML, CSS, and Github Pages Skill Path",
        "Web Development Career Path",
        "Laern JavaScript Course",
        "Learn JavaScript Unit Testing Course",
        "Learn Node.js Course",
        "Learn Express Course",
        "Learn jQuery Course",
        "Building Interactive JacaScript Websites Course",
        "Create a Front-End App with React Skill Path",
        "Create a Back-End App with JavaScript Skill Path",
        "Learn React Course",
        "Learn React.js: Part II Course",
        "How to Deploy a Website Course",
        "Learn SQL Course",
        "How to Transform Tables with SQL Course",
        "Learn Node SQLite Course",
        "Learn Navigation Design Course",
        "Learn Color Design Course",
        "Learn Sass Course"
    ]
    const codecademy = modules.map((course, index) => <p className="mb-2" key={index}>{course}</p>);

    return (
        <div className="skills-and-education-container">
            <p className="lead home-intro">Skills and Education</p>          
            <ul style={{listStyle: "none"}}>
                <li className="px-2"><i class="fab fa-github"></i></li>
                <li className="px-2"><i class="fab fa-html5"></i></li>
                <li className="px-2"><i class="fab fa-css3"></i></li>
                <li className="px-2"><i class="fab fa-js"></i></li>
                <li className="px-2"><i class="fab fa-node"></i></li>
                <li className="px-2"><i class="fab fa-react"></i></li>
                <li className="px-2"><i class="fab fa-mdb"></i></li>
                <li className="px-2"><i class="fab fa-sass"></i></li>
                <li className="px-2"><i class="fas fa-database"></i></li>
            </ul>
            <section className="education py-3">
                <EducationCard
                    course="Code Fellows of Seattle"
                    program="Full Stack JavaScript"
                    website="https://www.codefellows.org/"
                />
                <EducationCard
                    course="Udemy"
                    program="MERN eCommerce From Scratch"
                    website="https://www.udemy.com/course/mern-ecommerce/"
                />
                <EducationCard
                    course="Codewars"
                    program="Code Challenges - 3kyu Member"
                    website="https://www.codewars.com/"
                />
                <EducationCard
                    course="Codecademy"
                    program={codecademy}
                    website="https://www.codecademy.com/"
                />
            </section>
        </div>
    )
}

export default SkillsAndEducationScreen;